@import url(https://fonts.googleapis.com/css?family=Courgette);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
html {
  overflow: none;
  font-family: 'Roboto Slab', serif;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}
button {
  cursor: pointer;
  background-color: white;
}
img {
  user-drag: none; 
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
p {
  font-family: 'Roboto Slab', serif;
}
li {
  font-family: 'Roboto Slab', serif;
}
ul {
  list-style: none;
  padding-left:0;
}
body {
  overflow: none;
  font-family: 'Roboto Slab', serif;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
button {
  font-family: 'Roboto Slab', serif;
  border:none;
}
button:focus {
  outline: none;
}
.MainNav {
    width:100vw;
    padding-left:0.5vw;
    color:white;
    font-size: 5vh;
    font-weight: 600;
    background-color: white;
    box-shadow: 0px 2px 5px black;
    color:red;
    position: relative;
    z-index: 9999;
    /* padding-top:10px; */
}
.MainNav ul {
    display:flex;
    justify-content: space-around;
    height:40px;
    margin-top:0px;
    margin-bottom:0px;
    padding-left:0px;
    line-height:40px;
}
.MainNav li {
    /* line-height:60px; */
    cursor: pointer;
}
.MainNav img {
    position: relative;
    left:-1vw;
    top:-1vh;
    border-radius: 50%;
    display: inline-block;
    height: calc(100% + 3vh);
    border: 10px solid white;
    background-color:white;
    box-shadow: 0px 0px 6px black;
}
.MainNav img:active {
    box-shadow: 0px 0px 6px red;
}

.home {
    float: left;
    height:40px;
}
.home a {
    position: fixed;
}
.activeUser {
    margin-right:auto;
}
.logout {
    padding-right:2vw;
    color:green;
    cursor: pointer;
}
.pinContainer img {
  border-radius: 50%;
  display: inline-block;
  height: 150px;
  /* border: 10px solid white; */
  background-color:white;
  box-shadow: 0px 0px 6px black;
  margin-right:40px;
}
.activeSpecial {
  border: 2px solid red;
}
.inactiveSpecial {
  border: 10px solid white;
}
.inputPass input[type=number]{
  display:flex;
  width: 28vw;
  height: 150px;
  font-size: 150px;
  /* padding: 6vw; */
  padding:2.5vw;
  padding-bottom: 30px;
}
.inputPass input[type=password]{
  display:flex;
  width: 28vw;
  height: 150px;
  font-size: 150px;
  /* padding: 6vw; */
  padding:2.5vw;
  padding-bottom: 30px;
}
.loginPage {
  position: relative;
  display:flex;
  justify-content: space-evenly;
  flex-direction: column;
  height:inherit;
}
.loginSection {
  height:inherit;
}
.login {
  background: -webkit-linear-gradient(left, white 20px, transparent 1%) center, -webkit-linear-gradient(white 20px, transparent 1%) center, #ffcccc;
  background: linear-gradient(90deg, white 20px, transparent 1%) center, linear-gradient(white 20px, transparent 1%) center, #ffcccc;
  background-size: 22px 22px;
  color:#4CAF50;
  display:flex;
  justify-content: space-evenly;

  margin: auto 0;
  width: 100vw;
  height: 50vw;
  /* width:50vw;
  height:100vh; */
}
.pinContainer {
  position: fixed;
  display: flex;
  justify-content: space-evenly;
  justify-content: center;
  width:100vw;
  margin: auto;
  position: relative;
  /* flex-wrap: wrap; */
}
.pinBox {
  box-shadow: 0 0 5px black;
  margin: 2vh auto;
  border-radius: 15px;
  width:8vw;
  height: 18vh;
  font-size:30vh;
  line-height:20vh;
  color:black;
  background-color: white
}
.pinSection {
    box-shadow: -2px 0 10px black;
    background-color:#4CAF50;

    display:flex;

    width: 100vw;
    height: 50vw;
    /* width:50vw;
    height:inherit; */

    flex-wrap: wrap;
    height:inherit;
    align-content: flex-start;
    padding: 3vh 0;
  }
.pinSection button {  
  cursor: pointer;
  font-size: 10vh;
  width:15vw;
  height:20vh;
  margin:0.8vw;
  box-shadow: 0 0 10px black;
  border-radius: 25px;
}
.pinCommands button{
  width:23vw;
}
.clearPin {
  font-size:6vh!important;
  background-color: #ffcccc;
}
.specialUser {
  /* width:31vw!important; */
  font-size:4vh!important;
  /* background-color: #99ff99; */
  background-color: #a3a3a3;
}
.isSpecialActive {
  box-shadow: inset 0 0 10px #333!important;
  background-color: white!important;
  /* box-shadow: 0 0 0 black!important; */
}
.backSpace {
  background-color: #ffcccc;
}
.submit p, .backSpace p{
  font-size:5vh;
}
.pinSection button:active {
  box-shadow: inset 0 0 10px #333;
}
.isSpecialActive button:active {
  box-shadow: inset 0 0 10px white!important;
}
.submit {
  color:black;
  /* font-size:10vh; */

}
/* ##### ANIMATION LOGIN */
.animatePin-entering {
  left: 1vw;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.animatePin-entered {
  right: 1vw;
  -webkit-transition: 0.2s;
  transition: 0.2s
}
.animatePin-exiting{
  left: 1vw;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.animatePin-exited {
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

/* ##### END ANIMATION LOGIN */

