.CustomerTable{
    background-color: white;
    box-shadow: 0 0 10px black;
    border-radius: 5px;  
    width:1100px;
    height: 300px;
    margin: 0 auto;
} 

.CustomerTable table {
    margin:auto;
    width:90%;
    border-collapse: collapse;

}
.customerBasicInfo  {
    display: flex;
    height: 80px;
    font-size:30px;
    text-align: left;
}
.customerBasicInfo td {
    overflow-x: scroll;
    margin: auto 0;
    padding: 0px 10px;
    white-space: nowrap;
}
.customerBasicInfo td:nth-child(1) {
    width:160px;
}
.customerBasicInfo td:nth-child(2) {
    width:250px;
}
.customerBasicInfo td:nth-child(3) {
    width:450px;
}
.CustomerTable tbody {
    display:block;
    height:56vh;
    overflow:scroll;
    border-radius: 5px;  
}
.CustomerTable tr button{
    font-size: 20px;
    height: 50px;
    width:10vw;
    margin:5px;
    cursor: pointer;
    color:red;
    background-color: #ffcccc;
    border-radius: 5px;;
    box-shadow: 0 0 5px #ff3333;
}
.CustomerTable tr button:active {
    box-shadow: inset 0 0 10px #ff3333;
}
.CustomerTable tr:nth-child(even) {
    background-color: #ddd;
}
.CustomerTable tr:nth-child(even) button {
    color:#00b300;
    background-color: #99ff99;
    box-shadow: 0 0 10px #4CAF50;
}
.CustomerTable tr:nth-child(even) button:active {
    box-shadow: inset 0 0 20px #00b300;
}
.createCustomer {
    display: flex;
    height: 90%;
    flex-direction: column;
    width:81vw;
