    justify-content: space-between;
}
.createCustomer input {
    border-radius: 5px;
    box-shadow: 0 0 2px #333;
    height:70px;
}
.createCustomer input[type="text"]{
    font-size:5vh;
    padding-left:2vw;
}
.createCustomer input[type="number"]{
    font-size:5vh;
    padding-left:2vw;
}

.createCustomerBtn{
    padding: 0;
