/*  --- Pre-build Pizzas */
.defaultPizzas {
    position: fixed;
    display: flex;
    background-color: rgba(255,255,255,0.95);
    border-bottom-left-radius: 25px;
    box-shadow: 0 0 10px black;
    width:89vw;
    height: 20vh;
    overflow:auto;
    white-space: nowrap;
    border-radius: 25px;
    margin-bottom:0.1vh;
}
.defaultPizzas.hidePizzas {
    top:-20vh;
    background-color:rgba(255,0,110,90.95);
}
.defaultPizzas ul {
    padding: 0 2vw;
}
.pizzaShape {
    position: relative;
    width:8.5vw;
    height:15vh;
    background-color: rgb(255, 214, 102);
    border-radius: 50%;
    text-align: center;
    position: relative;
    cursor: pointer;
    box-shadow: 0 0 10px black;
}
.pizzaSauce {
    position: absolute;
    top:50%;
    left:50%;
    background-color: rgb(255, 64, 30);
    border-radius: 50%;
    width:7vw;
    height:12vh;
    margin-left:-3.5vw;
    margin-top:-6vh;
}
.pizzaChease {
    position: absolute;
    top:50%;
    left:50%;
    background-color: rgb(255, 238, 0);
    border-radius: 50%;
    width:6vw;
    height:11vh;
    margin-left:-3vw;
    margin-top:-5.5vh;
}

.pizzaName {
    white-space: pre-wrap;
    color:#333;
}
.pizzaName p {
    display: block;
    font-size:2.5vh;
    font-family: 'Courgette', cursive;
}
.createPizza {
    display:flex;   
    flex-direction: column;
    height: inherit;
    width: inherit;
}

