
.customerDisplay {
    position: relative;
    z-index: 222;
    height: 80vh;
    display: flex;
    flex-direction: column;
    /* overflow-x: scroll; */
}
.CustomerOptions {
    display: flex;
    justify-content: space-evenly;
    height:120px;
    margin:10px;
}
.customerTool {
    height: 80px;
    display: flex;
    margin: auto 0;
    justify-content: space-around;
    width: 100vw;
}
.activeTool {
    box-shadow: 0px 0px 10px #333;
    background-color: #00b300;
    color: white;
}
.inactiveTool {
    border: 1px solid #333;
    background-color: rgba(94, 94, 94, 0.2);
}
.customerTool button {
    height: 80px;
    font-size:30px;
    line-height: 10vh;
    cursor: pointer;
    border-radius: 5px;
}
.Searchbar {
    display:flex;
    margin: auto 0;
    width:22vw;
}
.Searchbar input {
    height:70px;
    width:19vw;
    border-radius: 5px;
    border: #00b300 solid 1px;
}
.Searchbar input[type="number"] {
    padding-left: 0.8vw;
    font-size:30px;
}

.CustomerWrapper {
    position: relative;
    z-index: 200;
    display:flex;
    justify-content: space-evenly;
    height: 320px;
    /* height: 100%; */
    /* margin:auto 0; */
    /* height: 100vh; */

}
/* .createCustomer input {
    margin
} */
.CustomerTable{
    background-color: white;
    box-shadow: 0 0 10px black;
    border-radius: 5px;  
    width:1100px;
    height: 300px;
    margin: 0 auto;
} 

.CustomerTable table {
    margin:auto;
    width:90%;
    border-collapse: collapse;

}
.customerBasicInfo  {
    display: flex;
    height: 80px;
    font-size:30px;
    text-align: left;
}
.customerBasicInfo td {
    overflow-x: scroll;
    margin: auto 0;
    padding: 0px 10px;
    white-space: nowrap;
}
.customerBasicInfo td:nth-child(1) {
    width:160px;
}
.customerBasicInfo td:nth-child(2) {
    width:250px;
}
.customerBasicInfo td:nth-child(3) {
    width:450px;
}
.CustomerTable tbody {
    display:block;
    height:56vh;
    overflow:scroll;
    border-radius: 5px;  
}
.CustomerTable tr button{
    font-size: 20px;
    height: 50px;
    width:10vw;
    margin:5px;
    cursor: pointer;
    color:red;
    background-color: #ffcccc;
    border-radius: 5px;;
    box-shadow: 0 0 5px #ff3333;
}
.CustomerTable tr button:active {
    box-shadow: inset 0 0 10px #ff3333;
}
.CustomerTable tr:nth-child(even) {
    background-color: #ddd;
}
.CustomerTable tr:nth-child(even) button {
    color:#00b300;
    background-color: #99ff99;
    box-shadow: 0 0 10px #4CAF50;
}
.CustomerTable tr:nth-child(even) button:active {
    box-shadow: inset 0 0 20px #00b300;
}
.createCustomer {
    display: flex;
    height: 90%;
    flex-direction: column;
    width:81vw;
    margin:0 auto;
    justify-content: space-between;
}
.createCustomer input {
    border-radius: 5px;
    box-shadow: 0 0 2px #333;
    height:70px;
}
.createCustomer input[type="text"]{
    font-size:5vh;
    padding-left:2vw;
}
.createCustomer input[type="number"]{
    font-size:5vh;
    padding-left:2vw;
}

.createCustomerBtn{
    padding: 0;
    margin:auto 0;
    height:11vh;
    width:20vw;
    margin-right:2vw;
    border-radius: 10px;
    font-size:4.5vh!important;
}
.invalidCreateBtn {
    background-color:rgba(94, 94, 94, 0.2);
    border:1px solid black;
}
.validCreateBtn {
    background-color:#99ff99;
    box-shadow: 0 0 15px #00b300;
    color:#00b300;
}

.createCustomerBtn:active {
    box-shadow: inset 0 0 15px #00b300;
}

/* SELECTED CUSTOMER */
.shaded img {
    filter: alpha(opacity=30);
    opacity: 0.1;
    /* background-color: transparent; */
}
.nextBtn, .prevBtn {
    margin:auto 5px;
}
.prevBtn {
    width: 50px;
}
.nextBtn {
    /* margin: auto; */
    width: 50px;
}
.nextBtn button {
    
}
.prevBtn img, .nextBtn img {
    width: 50px;

}
.nextBtn img {
    transform: rotate(180deg);
}

.customerHeader {
    display:flex;
    justify-content: center;
    height: 10vh;
    background-color: rgba(255, 255, 255, 0.5)
}
.customerHeader h1, h2 {
    margin: auto 0;
    padding: 0 2vw;
}
.customerHeader button{
    margin: auto 0;
    height:5vh;
    background-color: #ffcccc;
    width: 8vw;
    border-radius: 5px;
    font-size:3vh;
}
.previousOrderWrapper {
    display:flex;
    overflow-x:scroll;
    /* margin: 0 2vw; */
    height: 70vh;
}
.previousOrdersTable {
    display:block;
    margin: 1vh;
    border-radius: 5px;
    box-shadow: 0 0 5px black;
    background-color: white;
    min-width: 25vw;
    overflow-y:scroll;
}
.previousOrdersTable p {
    position: relative;
    top:0px;
}
.previousOrdersList {
    height: 40vh;
    border-radius: 5px;
    background-color: white;
    overflow-y:scroll;
    min-width: 25vw;
}
.previousOrdersTable table {
    overflow-y:scroll;
    background-color: white;
    display:flex;
    align-content: left;
    white-space: nowrap;
    width:25vw;
}
.previousOrdersTable table:nth-child(even) {
    background-color: #d6d6d6;
    color:#333;
}
.previousOrdersTable tbody {
    display:flex;
    flex-direction: column;
    width:25vw;
}
.previousOrdersTable tr {
    display:flex;
    height: 60px;
    overflow-x: scroll;
}
.previousOrdersTable td {
    padding:0 1vw;
}
.previousOrdersTable span {
    padding:0 1vw;
}
.previousOrdersTable button {
    position: relative;
    bottom: -5vh;
    height:10vh;
    width:20vw;
    /* margin: ; */
    border-radius: 5px;
    background-color: #99ff99;
    font-size: 30px;
}
