.dealsRender {
    white-space:nowrap;
}
.dealDisplay {
    display:flex;
    overflow-x: scroll;
}
.dealDisplay button {
    height:10vh;
    width:20vw;
    margin: auto auto 2vh auto;
    border-radius: 5px;
    background-color: #99ff99;
    font-size: 30px;
}
.dealDisplay table {
    background-color: white;
    box-shadow: 0px 0px 10px #333;
    border-radius: 5px;
    display:flex;
    width: 35vw;
    height: 90vh;
    margin:2vh;
}
.dealDisplay tbody {
    display:flex;
    flex-direction: column;
    width: 35vw;
    /* max-height: 90vh; */
}
.dealDisplay th {
    color:red;
    font-size:5vh;
}
.dealDisplay tr {
    max-height: 10vh;
    min-height: 9vh;
    margin:1vh 0;
    width:35vw;
    font-size:15px;
}
.dealDisplay td:first-child {
    padding:2vh;
    text-decoration: underline;
    font-weight: bold;
}
.dealDisplay tr:nth-child(even) {
    background-color: #d6d6d6;
    color:#333;
}
.dealDisplay td {
    padding: 1vw;
}