/* --- Render Area */

.navBtn {
    padding: 5vh 0;
    flex-grow: 1;
    width: 100%;
    font-size:5vh;
    cursor: pointer;
    background-color: transparent;
}
tn p { 
    background-color: white; 
    margin-left: 1vw;
    margin-right: 1vw;
}
.renderAreaOn img{
    filter: drop-shadow(5px 5px 4px #333);

}
.renderAreaOff img {
    filter: grayscale(100%);
}
.renderContainer {
    top:6vh;
    display:flex;
    width: 100vw;
    background: linear-gradient(90deg, white 20px, transparent 1%) center, linear-gradient(white 20px, transparent 1%) center, #ffcccc;
    background-size: 22px 22px;
}
.renderNav {
    display:flex;
    flex-direction: column;
    /* justify-content: space-around; */
    width: 10vw;
    /* height:20px; */
    box-shadow: 3px 0px 15px #333;
    border-radius: 5px;
    z-index: 8888;
    background: linear-gradient(90deg, #f9f9f9 20px, transparent 1%) center, linear-gradient(#f9f9f9 20px, transparent 1%) center, #00b300;
    /* background: linear-gradient(90deg, #6a6a6a 20px, transparent 1%) center, linear-gradient(#f9f9f9 20px, transparent 1%) center, #00b300; */
    background-size: 22px 22px;
}
.menuIcon {
    width:6.5vw;
}
.dealsIcon {
    width:6vw;
}
.slicesIcon {
    width:8.5vw;
}
.renderNav img {
    padding: 10px 0;
    
}
.renderArea {
    width:90vw;
    height: 100vh;
}

/*  --- Pre-build Pizzas */
.defaultPizzas {
    position: fixed;
    display: flex;
    background-color: rgba(255,255,255,0.95);
    border-bottom-left-radius: 25px;
    box-shadow: 0 0 10px black;
    width:89vw;
    height: 20vh;
    overflow:auto;
    white-space: nowrap;
    border-radius: 25px;
    margin-bottom:0.1vh;
}
.defaultPizzas.hidePizzas {
    top:-20vh;
    background-color:rgba(255,0,110,90.95);
}
.defaultPizzas ul {
    padding: 0 2vw;
}
.pizzaShape {
    position: relative;
    width:8.5vw;
    height:15vh;
    background-color: rgb(255, 214, 102);
    border-radius: 50%;
    text-align: center;
    position: relative;
    cursor: pointer;
    box-shadow: 0 0 10px black;
}
.pizzaSauce {
    position: absolute;
    top:50%;
    left:50%;
    background-color: rgb(255, 64, 30);
    border-radius: 50%;
    width:7vw;
    height:12vh;
    margin-left:-3.5vw;
    margin-top:-6vh;
}
.pizzaChease {
    position: absolute;
    top:50%;
    left:50%;
    background-color: rgb(255, 238, 0);
    border-radius: 50%;
    width:6vw;
    height:11vh;
    margin-left:-3vw;
    margin-top:-5.5vh;
}

.pizzaName {
    white-space: pre-wrap;
    color:#333;
}
.pizzaName p {
    display: block;
    font-size:2.5vh;
    font-family: 'Courgette', cursive;
}
.createPizza {
    display:flex;   
    flex-direction: column;
    height: inherit;
    width: inherit;
}

/*  --- Order Tabs */
.side-line {
    height:40px;
    border-bottom:1px solid black;
    width:40vw;
}
@media screen and (max-height: 410px) {
    .pizzaState {
        display: none;
    }
}
.pizzaState {
    /* background: linear-gradient(90deg, white 20px, transparent 1%) center, linear-gradient(white 20px, transparent 1%) center, #00b300;
    background-size: 22px 22px; */
    background-color: #f9f9f9;
    /* background-color: #aaaaaa; */
    z-index: 799;
    position: relative;
    padding:1vh 1vw;
    box-shadow: 0 0 15px #333;
}
.menuBarLine {
    position: relative;
    height: 1px;
