/*   --- TOPPINGS BOX */
.toppingContainer {
    width: inherit;
    display:flex;
    flex-direction: column;
    margin: 0 auto;
}
.toppingsRight img, .toppingsLeft img {
    filter: drop-shadow(5px 5px 5px #333);
}
.toppingsRight img{
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}
.ActiveSide {
    border-radius: 5px;
    background-color: #ffE5E5;
    box-shadow: 0 0 20px black;
}
.InactiveSide {
    filter: grayscale(1)
}
.createWhatDisplay, .toppingsSplit {
    background: linear-gradient(90deg, white 20px, transparent 1%) center, linear-gradient(white 20px, transparent 1%) center, rgb(50, 255, 57);
    background-size: 22px 22px;
    box-shadow: inset 0 0 10px black;
    display: flex;
    flex-direction: row;
    position: relative;
    z-index: 10;
    height: 80vh;
    width: 90vw;
}
.createWhatDisplay button, .toppingsSplit button {
    display:flex;
    margin: 0 auto;
    height: 100%;
    border-radius: 5px;
    background: transparent;
    font-size: 10vh;
    cursor: pointer;
}
.createWhatDisplay img, .toppingsSplit img {
    max-height: 100%;
    max-width: 100%;
    filter: drop-shadow(5px 5px 5px #333);
}
.hidden {
    display: none;
}
.toppingsBox {
    height: 65vh;
    display:flex;
    box-shadow: inset 0 0 5px black;
}
.toppingsBox li {

    box-shadow: 0 0 10px black;
}
