/*  USER INFO */
.userInfo {
    position: fixed;
    height: 100vh;
    width: 100vw;
    left:0vw;
    top:6vh;
    background-color: #1f1f23;
    border-radius: 5px;
}
.userInfo h1 {
    padding-bottom: 2vh;
}
.userInfoHeader li{
    margin:auto;
}
.userInfoHeader {
    display:flex;
    /* justify-content: space-evenly; */
    margin:0 auto;
}

.hideUserInfo {
    display:none;
}
.closeUserInfo {
    width:8vw;
    height: 60px;
    background-color: #333;
    border:1px solid #333;
}
.closeUserInfo p {
    font-size:20px;
}
.userInfoContainer {
    display:flex;
    justify-content: space-evenly;
}
.userOrderList {
    display:flex;
    justify-content: space-evenly;
    font-size:3.5vh;
    width:100vw;
}

/* DETAILED ORDERS */
.detailedItem {
    width: 100vw;
    overflow-x:scroll;
    background-color: #333;
}
.detailedOrderWrapper {
    display:flex;
    width:100vw;
}
.detailedItem table {
    display:flex;
    background-color: #4E4E58;
    width:30vw;
    margin:2vh 1vw;
    height:80vh;
    border-radius: 10px;
    box-shadow: 5px 5px 5px black;
    margin-bottom: 4vh;
    border-collapse: collapse;
    /* overflow-y:scroll; */
}
.detailedItem tbody {
    display:block;
    width: 30vw;
    border-radius: 10px;
    overflow-y: scroll;
    /* padding: 1vw; */
} 
.detailedItem tr {
    font-size:3vh;
    overflow-x: scroll;
    height: 10vh;
    display:flex;
    justify-content: space-between;
    /* box-shadow: 0 0 5px black; */
    padding:0 2vw;
}
.detailedItem td:first-child {
    padding: 0;
}
.detailedItem td {
    text-align: left;
    /* width:20vw; */
    
}
.meatDetailed td, .veggiesDetailed td, .cheeseDetailed td, .sauceDetailed td{
    padding:0 2vw;
}
.detailedOrderHeader{
    width: 47vw;
    padding: 1vh;
    display:flex;
    margin-left:8vw;
    justify-content: space-between;
    color:#767676;
}

/* END DETAILED ORDERS */

/* ALL ORDERS */
.allOrderHeader ul{
    width: 72vw;
    display:flex;
    margin:0 0 0 7vw;
    justify-content: space-between;
    color:#767676;
}
.allOrderHeader li:nth-child(1) {
    width: 15vw;
}
.allOrderHeader li:nth-child(2) {
    width: 20vw;
}
.allOrderHeader li:nth-child(3) {
    width: 15vw;
}
.allOrderHeader li:nth-child(4) {
    width: 7vw;
}

.allItems{
    font-size:4vh;
    width:100vw;
    background-color: #333;
    display: flex;
    text-align: left;
    justify-content: space-evenly;
    flex-direction: column;
}
.allItems table {
    display: block;
    margin:1vh 2vw;
    margin-bottom: 4vh;
    height: 70vh;
    background-color: #4E4E58;
    box-shadow: 5px 5px 5px black;
    border-radius: 10px;
    border-collapse: collapse;
    overflow-y:scroll;
}
.allItems tbody{
    /* height: 5px; */
    width:100%;
    border-radius: 10px;
    overflow:scroll;
}
.allItems tr {
    display:flex;
    width:96vw;
    height: 12vh;
    /* line-height: 8vh; */
    /* align-content: left; */
    justify-content: space-between;
}
.viewOrder {
    width:15vw;
    font-size: 3vh;
    padding: 1vw;
    margin-right: 2vw;
    border-radius: 5px;
}
.allItems tr button {
    background-color: #2E2E34;
    color: white;
}
.allItems tr:nth-child(even) button {
    background-color: #767676;
    color:#333;
}
.allItemsTable td:nth-child(1){
    margin-left:2vw;
    width:12vw;
}
.allItemsTable td:nth-child(2) {
    width:25vw;
    word-wrap: break-word;
}
.allItemsTable td:nth-child(3){
    width:15vw;
}
.allItemsTable td:nth-child(4) {
    width:2vw;
}
.allItemsTable td:nth-child(5){
    width:10vw;
}
/* .o_extraInfo {
    width:100vw!important;
} */
.allItems tr:nth-child(even),.detailedItem tr:nth-child(even) {
    background-color: #aaa;
    color:#333;
    /* box-shadow: 0 0 5px black; */
}
.allItems td,.detailedItem td {
    margin: auto 0;
}
/* .allItems td,.detailedItem td {
    display:flex;
    line-height: 10vh;
    padding:2vh 1vw;
    text-align: left;
    width:18vw;
} */
.orderTable {
    font-size:4vh;
    width:29vw;
    background-color: #333;
    border-radius: 5px;
    margin: 0 0.5vw;
    height:80vh;
    display: flex;
    flex-direction: column;
    overflow-y:scroll;
}
.orderTable table {
    width: 27vw;
    height: 30vh;
    margin: 1vw;
    background-color: #4E4E58;
    box-shadow: 5px 5px 5px black;
    border-radius: 5px;
}

.orderTable tr {
    padding: 2vh 0;
    height:15vh;
    margin-left:-0.15vw;
    display:flex;
    justify-content: space-between;
    /* background-color: white; */
    width: 26.9vw;
    overflow: scroll;
} 
.orderTable tr:nth-child(even) {
    background-color: #aaa;
    color:#333;
    /* box-shadow: 0 0 5px black; */
}
.orderTable td {
    padding: 0 0.5vw;
}


/* end of USER INFO */


/* user Settings */
.removeUser {
    display:flex;
    justify-content: space-evenly;
    flex-direction: column;
    position: fixed;
    top:30vh;
    left:25vw;
    width: 50vw;
    height: 30vh;
    background-color:red;
    border-radius: 5px;
}

.removeUserBtn {
    height:5vh;
    margin: auto 5vw;
    /* margin-left: 10vw; */
    border-radius: 5px;
    background-color: #333;
    /* background-color: #4E4E58; */
    color:white;
    /* border: 1px solid rgb(255, 106, 106); */
}
.removeOptions {
    display:flex;
    justify-content: space-around;
}
.removeOptions button {
    width:15vw;
    height:10vh;
    border-radius: 5px;
    font-size:5vh;
}
.searchDate {
    height:6vh;
    width:13vw;
    margin: auto 0;
    font-size:4vh;
    border-radius: 5px;
    background-color: #767676;
    color:#333;
}
.userInfoHeader input {
    /* line-height: 5vh; */
    margin: auto 0;
    font-size:3vh;
    padding: 0 0.5vw;
    width:25vw;
    letter-spacing: 1vw;
    height: 5vh;
    box-shadow: 0 0 5px #333;
    /* margin: 0 1vw; */
    border-radius: 5px;
}
