.MainNav {
    width:100vw;
    padding-left:0.5vw;
    color:white;
    font-size: 5vh;
    font-weight: 600;
    background-color: white;
    box-shadow: 0px 2px 5px black;
    color:red;
    position: relative;
    z-index: 9999;
    /* padding-top:10px; */
}
.MainNav ul {
    display:flex;
    justify-content: space-around;
    height:40px;
    margin-top:0px;
    margin-bottom:0px;
    padding-left:0px;
    line-height:40px;
}
.MainNav li {
    /* line-height:60px; */
    cursor: pointer;
}
.MainNav img {
    position: relative;
    left:-1vw;
    top:-1vh;
    border-radius: 50%;
    display: inline-block;
    height: calc(100% + 3vh);
    border: 10px solid white;
    background-color:white;
    box-shadow: 0px 0px 6px black;
}
.MainNav img:active {
    box-shadow: 0px 0px 6px red;
}

.home {
    float: left;
    height:40px;
}
.home a {
    position: fixed;
}
.activeUser {
    margin-right:auto;
}
.logout {
    padding-right:2vw;
    color:green;
    cursor: pointer;
}