.pinContainer img {
  border-radius: 50%;
  display: inline-block;
  height: 150px;
  /* border: 10px solid white; */
  background-color:white;
  box-shadow: 0px 0px 6px black;
  margin-right:40px;
}
.activeSpecial {
  border: 2px solid red;
}
.inactiveSpecial {
  border: 10px solid white;
}
.inputPass input[type=number]{
  display:flex;
  width: 28vw;
  height: 150px;
  font-size: 150px;
  /* padding: 6vw; */
  padding:2.5vw;
  padding-bottom: 30px;
}
.inputPass input[type=password]{
  display:flex;
  width: 28vw;
  height: 150px;
  font-size: 150px;
  /* padding: 6vw; */
  padding:2.5vw;
  padding-bottom: 30px;
}
.loginPage {
  position: relative;
  display:flex;
  justify-content: space-evenly;
  flex-direction: column;
  height:inherit;
}
.loginSection {
  height:inherit;
}
.login {
  background: linear-gradient(90deg, white 20px, transparent 1%) center, linear-gradient(white 20px, transparent 1%) center, #ffcccc;
  background-size: 22px 22px;
  color:#4CAF50;
  display:flex;
  justify-content: space-evenly;

  margin: auto 0;
  width: 100vw;
  height: 50vw;
  /* width:50vw;
  height:100vh; */
}
.pinContainer {
  position: fixed;
  display: flex;
  justify-content: space-evenly;
  justify-content: center;
  width:100vw;
  margin: auto;
  position: relative;
  /* flex-wrap: wrap; */
}
.pinBox {
  box-shadow: 0 0 5px black;
  margin: 2vh auto;
  border-radius: 15px;
  width:8vw;
  height: 18vh;
  font-size:30vh;
  line-height:20vh;
  color:black;
  background-color: white
}
.pinSection {
    box-shadow: -2px 0 10px black;
    background-color:#4CAF50;

    display:flex;

    width: 100vw;
    height: 50vw;
    /* width:50vw;
    height:inherit; */

    flex-wrap: wrap;
    height:inherit;
    align-content: flex-start;
    padding: 3vh 0;
  }
.pinSection button {  
  cursor: pointer;
  font-size: 10vh;
  width:15vw;
  height:20vh;
  margin:0.8vw;
  box-shadow: 0 0 10px black;
  border-radius: 25px;
}
.pinCommands button{
  width:23vw;
}
.clearPin {
  font-size:6vh!important;
  background-color: #ffcccc;
}
.specialUser {
  /* width:31vw!important; */
  font-size:4vh!important;
  /* background-color: #99ff99; */
  background-color: #a3a3a3;
}
.isSpecialActive {
  box-shadow: inset 0 0 10px #333!important;
  background-color: white!important;
  /* box-shadow: 0 0 0 black!important; */
}
.backSpace {
  background-color: #ffcccc;
}
.submit p, .backSpace p{
  font-size:5vh;
}
.pinSection button:active {
  box-shadow: inset 0 0 10px #333;
}
.isSpecialActive button:active {
  box-shadow: inset 0 0 10px white!important;
}
.submit {
  color:black;
  /* font-size:10vh; */

}
/* ##### ANIMATION LOGIN */
.animatePin-entering {
  left: 1vw;
  transition: 0.2s;
}
.animatePin-entered {
  right: 1vw;
  transition: 0.2s
}
.animatePin-exiting{
  left: 1vw;
  transition: 0.2s;
}
.animatePin-exited {
  transition: 0.2s;
}

/* ##### END ANIMATION LOGIN */