.adminPanel {
    display: flex;
    width: 100vw;
    height: 100vh;
    /* min-height: 800px; */
    justify-content: row;
    background-color: #333;
    color:white;
}
.adminArea {
    background-color: #333;
    width:100%;
    height: 100%;
}
.panelNav {
    display: flex;
    flex-direction: column;
    height: 95vh;
    width:8vw;
    padding-left:1vw;
    padding-right:1vw;
    background-color: #1A1A1A
}
.panelNav button {
    height: 100%;
    font-size: 5vh;
    border-radius: 5px;
    margin: 2vh 0;
    color: white;
}
.activePanel {
    background-color: #099996;
    box-shadow: 0 0 20px black;
}
.notActivePanel {
    background-color: #1A1A1A;
}
.accountsRender {
    padding:0 1vw;
    height: 100%;
}
.accountsOptions {
    padding:0 1em;
    text-align: left;
}
.accountsRender h1 {
    margin: 5px 0 0 0;
}
.activeAccounts {
    height: 460px;
    margin: auto 0;
    background-color: #4E4E58;
    box-shadow: 5px 5px 5px black;
    border-radius: 5px;
    overflow: scroll;
    margin-bottom: 8vh;
}
.accountHeadings {
    width:100%;
    display: flex;
    margin: 2vh 0;
    background-color: #4E4E58;
    box-shadow: 5px 5px 5px black;
    border-radius: 5px;
    padding: 2vh 0;
    text-emphasis: bold;
    font-size:3vh
}
.accountHeadings tbody{
    display: flex;
    flex-direction: row;
    width:100%;
    justify-content: space-between;
}
.accountHeadings tr {
    display: flex;
    flex-direction: row;
    width:100%;
    justify-content: space-evenly;
}

.activeAccounts table {
    width:100%;
    border-collapse: collapse;
    border-radius: 5px;
    font-size: 30px;
    padding: 0.5em;
    padding-top:10vh;
}
.activeAccounts tbody {
    margin-top:10vh;
}
.activeAccounts td {
    height: 80px;
    padding: 1vh 1vh;
}
.activeAccounts tr:nth-child(even) {
    background-color: #aaa;
    box-shadow: 0 0 5px #333;
    color:#333;
}

.activeAccounts button {
    background-color: #2E2E34;
    color: white;
}
.activeAccounts tr:nth-child(even) button {
    background-color: #767676;
    color:#333;
}
.activeAccounts tr:last-child {
    width:5vw;
}
.createAccountsWrapper {
    background-color: #4E4E58;
    border-radius: 5px;
    box-shadow: 5px 5px 5px black;
    padding:1vh 0 2vh 0;
    margin:2.5vh 2vw;
    height: 90px;
}
.createAccountsWrapper h2 {
    margin: 1vh 0;
}
.createAccount {
    display:flex;
    justify-content: space-evenly;
    padding: 0 2vw;
    height: 100%;
}
.createAccount input {
    font-size:30px;
    padding: 0 0.5vw;
    width:80%;
    height: 35px;
    box-shadow: 0 0 5px #333;
    margin: 0 1vw;
    border-radius: 5px;
}
.createAccount button {
    width:15vw;
    box-shadow: 0 0 5px #333;
    border-radius: 5px;
}
.accountFirst {
    width:40vw;
}
.accountPin {
    width:10vw;
}
.viewUserInfo {
    font-size:15px;
    padding: 0.4vh 1vw;
    margin-right:2vw;
    border-radius: 5px;
    float: right;
    height: 50px;
}


